import Logo1 from './logo1'
import Logo2 from './logo2'
import Logo3 from './logo3'
import Logo4 from './logo4'
import Logo5 from './logo5'
import Logo6 from './logo6'
import Logo7 from './logo7'
import Logo8 from './logo8'
import Logo9 from './logo9'
import Logo10 from './logo10'



export type LogoSVGImport = {
	id: string
	svg: string
}

// Export a obejct with all the logos
// Useful for importing only some specific logos
export {
	Logo1,
	Logo2,
	Logo3,
	Logo4,
	Logo5,
	Logo6,
	Logo7,
	Logo8,
	Logo9,
	Logo10
}

// Export a list with all the logos
// Useful for rendering a select input or a preview with all the logos available
export default [
	Logo1,
	Logo2,
	Logo3,
	Logo4,
	Logo5,
	Logo6,
	Logo7,
	Logo8,
	Logo9,
	Logo10
]
